* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	width: 100%;
	padding: 32px;

	background-color: #333;
}

body, input {
	font-family: 'Roboto', 'Tahoma', 'Open Sans', sans-serif;
	font-size: 13px;
	color: #FFF;

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

input {
	width: 100%;
	height: 100%;
	min-height: 32px;
	padding: 0 8px;
	border-radius: 4px;
	border: none;
	background-color: hsla(0, 0%, 100%, 10%);
}

button {
	cursor: pointer;
}

main {
	width: 100%;
	max-width: 640px;
	min-width: 640px;
}

@media all and (max-width: 840px) {
	main {
		max-width: none;
	}
}


table {
	width: 100%;
	border-spacing: 0;

	tr {
		td {
			height: 32px;
			border-top: 1px solid hsla(0, 0%, 100%, 10%);
			text-align: right;
			padding-right: 12px;

			&.td-head {
				padding-left: 8px;
				text-align: left;
			}

			&.td-input {
				padding: 4px 2px;

				input {
					min-height: 100%;
					text-align: center;
				}
			}

			&.add-year {
				height: 40px;
				text-align: center;

				div {
					width: 100%;
					height: 100%;

					display: flex;
					flex-direction: row;
					justify-content: center;
					align-items: center;

					button {
						width: 100%;
						height: 100%;

						display: flex;
						justify-content: center;
						align-items: center;
						background-color: transparent;
						border: none;

						color: #FFF;

						svg {
							width: 16px;
							margin-right: 4px;

							path {
								fill: #FFF;
							}
						}
					}
				}
			}

			&.separator {
				height: 4px;
				background-color: hsla(0, 0%, 100%, 30%);
			}
		}
	}
}

.txt-yellow {
	color:hsl(40, 75%, 75%);
}
